ario (marcada con un círculo) deben ser usadas en equipos sensibles no conectados a pacientes o en hospitales, respectivamente.")])]),_c('div',{staticClass:"col-md-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/video2-03.png"),"alt":""}})])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Tomas a 220"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-8 mb-4 mb-md-0"},[_c('p',[_vm._v("Las tomas a 220 o trifásicos también deben ser “polarizados”, es decir, la forma y disposición de los terminales debe ser tal, que garanticen una correcta conexión eléctrica bajo cualquier condición. ")]),_c('p',[_vm._v("Los requisitos de producto y de instalación de clavijas y tomacorrientes están establecidos en el numeral 20.10 del "),_c('i',[_vm._v("RETIE")]),_vm._v(". ")])]),_c('div',{staticClass:"col-md-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/video2-04.png"),"alt":""}})])])])])]),_c('separador'),_vm._m(33),_vm._m(34),_vm._m(35),_vm._m(36),_vm._m(37),_c('separador'),_vm._m(38),_vm._m(39),_vm._m(40),_vm._m(41),_c('p',{staticClass:"mt-5",attrs:{"data-aos":"fade-left"}},[_vm._v("El calibre mínimo de los conductores de los circuitos ramales y la capacidad de los interruptores automáticos correspondientes a cada circuito se muestran en la siguiente tabla:")]),_vm._m(42),_vm._m(43),_vm._m(44),_c('div',{staticClass:"tarjeta tarjeta--azul p-4 mb-5 mt-5"},[_c('SlyderA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Los motores y generadores eléctricos son máquinas que convierten la energía eléctrica en mecánica, los motores y la energía mecánica en eléctrica; los generadores gracias a la acción de dos principios electromagnéticos fundamentales: la Ley de inducción de Faraday, que establece que en cualquier conductor que se encuentre dentro de un campo magnético variante en el tiempo se “induc